
import ReportServices from 'Services/Api/Reports/Reports'
import { getNestedObjects, getNestedArrayOfObjects } from 'Pages/Protected/Partner/Components/Utils/utils'

export const getAffiliatedHospitalInfiniteScroll = async (setHospitals, hospitalData, offset, limit, setTotalCounts, filter, type = 'HOSPITAL') => {
  const { data } = await ReportServices.getFacilitiesByTypeForPreviewAndDownload(type, { skip: offset, limit, filter })
  if (data.data) {
    const { data: hospitals, count } = data.data

    if (type === 'HOSPITAL' || type === 'CLINIC') {
      const modifiedData = hospitals.map(items => {
        return {
          status: getNestedObjects(items, ['status']),
          classification: getNestedObjects(items, ['classification']),
          id: getNestedObjects(items, ['id']),
          name: getNestedObjects(items, ['name']),
          street: getNestedObjects(items, ['street']),
          barangay: getNestedObjects(items, ['barangay']),
          city: getNestedObjects(items, ['city']),
          district: getNestedObjects(items, ['district']),
          region: getNestedObjects(items, ['region']),
          cNumber: getNestedArrayOfObjects(items, ['contacts']).length > 0
            ? getNestedObjects(items, ['contacts']).map(items => getNestedObjects(items, ['cNumber'])).join(' ,')
            : ''
        }
      })
      const prevHospitals = [...hospitalData]

      setTotalCounts(count)
      setHospitals([...prevHospitals, ...modifiedData || []])
    } else {
      const prevHospitals = [...hospitalData]
      const modifiedData = hospitals
      setTotalCounts(count)
      setHospitals([...prevHospitals, ...modifiedData || []])
    }
  }
}

