export const modifyFilters = (filter = {}, withPrefix = true) => {
  const addressKey = ['street', 'barangay', 'city', 'district', 'province', 'region']
  const obj = {}
  for (let key in filter) {
    if (addressKey.includes(key)) {
      if (withPrefix) {
        obj[`address.${key}`] = { $regex: filter[key], $options: 'i' }
      } else {
        obj[key] = { $regex: filter[key], $options: 'i' }
      }
    } else {
      obj[key] = { $regex: filter[key], $options: 'i' }
    }
  }
  return obj
}

