import React from 'react'
import PdfViewer from 'SharedComponents/PdfViewer/PdfViewer'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import { useParams } from 'react-router-dom'
// import Shared Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbsNoStepper from '../../../Components/HeaderWithBreadCrumbsNoStepper/HeaderWithBreadCrumbsNoStepper'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import ContractManagerServices from 'Services/Api/Partner/ContractManagerServices'
import ListOfAffiliatedDoctors from 'Services/Api/Partner/ListOfAffiliatedDoctors'
import { MozillaPdfURL } from 'SharedComponents/PdfViewer2/PdfViewer2'
import useAsyncHook from '../../../CustomHooks/useAsyncHook2'

import classes from './ContractPage.module.scss'

const ContractPage = props => {
  const { type = '', id = '', facilityId = '' } = useParams()
  const [isLoaded, setIsLoaded] = React.useState(false)
  const [fileName, setFileName] = React.useState('')
  const [service, setService] = React.useState('')

  const [data, error, loading, execute] = useAsyncHook(ListOfAffiliatedDoctors.getAffiliatedDoctors)

  React.useEffect(() => {
    setIsLoaded(true)
    BreadcrumbsService.setSelectedItemsWithoutHome(['CONTRACT_MANAGER', 'PRINT_CONTRACT'])
    try {
      if (facilityId) {
        setService(ContractManagerServices.getContract({ type, id, facilityId }))
        setFileName(`${id}_${facilityId}_contract`)
      } else {
        if (type === 'dental_clinic') {
          execute({ id, type: 'DENTAL_CLINIC', limit: 10, offset: 0, filter: {} })
        } else {
          setService(ContractManagerServices.getContract({ type, id }))
          setFileName(`${id}_contract`)
        }
      }
    } catch (err) {
      props.history.push('/contractmanager')
    }
  }, [])

  React.useEffect(() => {
    const keys = Object.keys(data)
    if (keys.length > 0) {
      if (data.data.list.length > 0) {
        const dentistID = data.data.list[0].id
        setService(ContractManagerServices.getContract({ type: 'dentist', id: dentistID }))
        setFileName(`${dentistID}_contract`)
      }
    }
  }, [data])

  React.useEffect(() => {
    if (loading) {
      LoadingService.loading(true)
    } else {
      LoadingService.loading(false)
    }
  }, [loading])

  // const downloadData = async ({ type, id, facilityId = '' }) => {
  //   LoadingService.loading(true)
  //   const res = await ContractManagerServices.getContract({ type, id, facilityId })
  //   const url = URL.createObjectURL(new Blob([res.data]))
  //   LoadingService.loading(false)
  //   const link = document.createElement('a')
  //   link.href = url
  //   link.setAttribute('download', `Contract_${type}_${id}.pdf`)
  //   document.body.appendChild(link)
  //   link.click()
  // }

  // const onPrintContract = async ({ type, id, facilityId = '' }) => {
  //   LoadingService.loading(true)
  //   const res = await ContractManagerServices.getContract({ type, id, facilityId })
  //   const blob = new Blob([res.data])
  //   if (window.ipcRenderer) {
  //     let reader = new FileReader()
  //     reader.readAsDataURL(blob)
  //     reader.onloadend = () => {
  //       const content = reader.result.split(',')
  //       const base64data = content[1]
  //       window.ipcRenderer.send('print-to-pdf', { data: base64data, filename: 'TEST_PDF' })
  //     }
  //   } else {
  //     PrintPDFService(blob)
  //   }
  //   LoadingService.loading(false)
  // }

  return (
    <React.Fragment>
      <div id='mainContractPage' className={classes.root}>
        <HeaderWithBreadCrumbsNoStepper id='header' HeaderTitle='Medical Partner Contract' />
        <Grid container className={classes.content}>
          <Grid item xs={2} />
          <Grid item xs={8}>
            {isLoaded && (
              <MozillaPdfURL name={fileName} service={service} enabled />
              // <PdfViewer
              //   file={file}
              //   print={() => onPrintContract({ type, id, facilityId })}
              //   download={() => downloadData({ type, id, facilityId })}
              // />
            )}
          </Grid>
          <Grid item xs={2} className={classes.buttonContainer}>
            <Button
              variant='contained'
              className={classes.button}
              // onClick={e => onPrintContract({ type, id, facilityId })}
            >
              Print Contract
            </Button>
          </Grid>
        </Grid>
      </div>
    </React.Fragment>
  )
}

export default ContractPage
