import instance from '../instance'
import BaseUrlFetcher from 'Services/BaseUrlFetcher'

const facilities = instance('/p/f')

export default class Contracts {
  static async getContract(paramId, type, facilityId) {
    // return facilities({
    //   url:          `/partners/profile/contractDetails/${type}/${paramId}/${facilityId}`,
    //   method:       'GET',
    //   responseType: 'blob'
    // })

    return new Promise((resolve, reject) => {
      const token = window.localStorage.getItem('token') || window.localStorage.getItem('access_token')
      const baseURL = BaseUrlFetcher.getPartnerBaseUrl()
      const url = `${baseURL}/p/f/partners/profile/contractDetails/${type}/${paramId}/${facilityId}?token=${token}`
      resolve(url)
    })
  }
}
