import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

let notRequiredFields = [
  'classification',
  'processType'
]

const schema = Joi.object({
  nonVatable: Joi.string()
    .label('Tin Status Value')
    .required(),
  tinStatus: Joi.string()
    .label('Vat Status')
    .required()
    .messages({
      'string.empty': `"VAT Status" is required`,
      'any.required': `"tinStatus" is a required`
    }), // required
  vatTaxRate: Joi.number()
    .label('Tax Rate')
    .positive()
    .required() // required,
})
let newErrors = {}

export default function TinStatusValidation (form, errors) {
  newErrors = { ...errors }
  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      newErrors[i.path[0]] = i.message
      if (form['tinStatus'] === '12%' || form['tinStatus'] === '') {
        delete newErrors['nonVatable']
      } else {
        newErrors['nonVatable'] = '"VAT Status value" is required'
      }
      if (i.context.key === 'vatTaxRate') {
        let errorMessage = ''
        errorMessage = i.type === 'number.positive' ? '"TAX Rate" must be a positive number' : '"TAX Rate" is required'
        newErrors['vatTaxRate'] = errorMessage
      }
      if (notRequiredFields.includes(i.path[0])) {
        delete newErrors[i.path[0]]
      }
    }
    console.log('tin status new erorrs', newErrors)
  }

  return {
    newErrors,
    isValid: isEmpty(newErrors)
  }
}
