import React from 'react'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'

export default function RemoveConfirmationDialog ({ open = false, setOpen, handleRemove: remove }) {
  const handleRemove = () => {
    remove()
    setOpen(false)
  }
  return (
    <div>
      <Dialog
        open={open}
        onClose={() => setOpen(false)}
        aria-labelledby='alert-dialog-title'
        aria-describedby='alert-dialog-description'
      >
        <DialogTitle id='alert-dialog-title' />
        <DialogContent style={{textAlign: 'center', fontWeight: '800', fontSize: '1em'}} id='alert-dialog-description'>
          Are you sure you want to remove this file?
        </DialogContent>
        <DialogActions style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
          <Button style={{ backgroundColor: '#fd5451', color: 'white', padding: '0.6em', marginRight: '0.6em', textTransform: 'capitalize' }} onClick={() => setOpen(false)} color='primary'>
            No
          </Button>
          <Button style={{backgroundColor: '#3ab77d', color: 'white', padding: '0.6em', marginLeft: '0.6em', textTransform: 'capitalize'}} onClick={handleRemove} color='primary' autoFocus>
            Yes
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
