import React from 'react'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'

export default function DisapproveConfirmationDialog ({ open = false, setOpen, handleDisapproved: disapproved }) {
  const handleDisapproved = () => {
    disapproved()
    setOpen(false)
  }
  return (
    <div>
      <Dialog
        open={open}
        onClose={() => setOpen(false)}
        aria-labelledby='alert-dialog-title'
        aria-describedby='alert-dialog-description'
      >
        <DialogTitle id='alert-dialog-title' />
        <DialogContent style={{textAlign: 'center', fontWeight: '700', fontSize: '1.1em'}}>
        Are you sure you want to disapprove the Affiliation form?
        </DialogContent>
        <DialogActions style={{ display: 'flex', justifyContent: 'center' }}>
          <Button style={{backgroundColor: '#fd5451', color: 'white', padding: '0.5em', textTransform: 'capitalize', marginRight: '0.6em'}} onClick={() => setOpen(false)} color='primary'>
            Cancel
          </Button>
          <Button style={{backgroundColor: '#1ab1c4', color: 'white', padding: '0.5em', textTransform: 'capitalize', marginLeft: '0.6em'}} onClick={handleDisapproved} color='primary' autoFocus>
            Disapprove
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
