// #region Global Imports
import * as React from 'react'
import { useState } from 'react'
import { useSnackbar } from 'notistack'
import { accountsPageStyles } from './style'
import CreateIcon from '@material-ui/icons/Create'
import {
  Grid,
  IconButton,
  CircularProgress,
  Avatar,
  Fade,
  Backdrop,
  Modal,
  List,
  Link,
  ListItemText
} from '@material-ui/core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes } from '@fortawesome/free-solid-svg-icons'
import ImageCropper from '../ProfilePicCropper'

export default function ProfilePage({ setProfilePic64, profilePic64, isToUpdate, previousProfilePic }) {
  const { enqueueSnackbar } = useSnackbar()
  const classes = accountsPageStyles()
  const [loading, setLoading] = useState(false)
  const [userData, setUserData] = useState([])

  React.useEffect(() => {
  }, [])

  const handleUpload = (imgBase64 = '') => {
    setUserData({
      ...userData,
      profile_pic: (imgBase64).split(',')[1]
    })
    if (typeof setProfilePic64 === 'function') {
      setProfilePic64((imgBase64).split(',')[1])
    }
    setUploadModal(false)
  }

  const openUploadModal = () => {
    setUploadModal(true)
  }

  const closeUploadModal = () => {
    setUploadModal(false)
    // setUserData(originalData)
    setTemp64(null)
  }

  const handleAccountPhoto = (event) => {
    const validFiles = ['image/jpeg', 'image/jpg,', 'image/png', 'image/svg+xml']

    if (validFiles.includes(event[0].type)) {
      let reader = new FileReader()
      reader.readAsDataURL(event[0])
      reader.onload = () => {
        setTemp64(reader.result)
      }
    } else {
      const message = 'Invalid file extension.'
      enqueueSnackbar(message, {
        variant: 'error',
        anchorOrigin: {
          vertical: 'top',
          horizontal: 'right'
        }
      })
    }
  }

  const [uploadModal, setUploadModal] = React.useState(false)
  const [temp64, setTemp64] = React.useState(null)

  return (
    <React.Fragment>
      <Modal
        aria-labelledby='transition-modal-title'
        aria-describedby='transition-modal-description'
        className={classes.modal}
        open={uploadModal}
        onClose={closeUploadModal}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500
        }}>
        <Fade in={uploadModal}>
          <div className={classes.paper}>
            <List>
              <ListItemText>
                <Link
                  className={classes.closeButton}
                  onClick={closeUploadModal}>
                  <FontAwesomeIcon icon={faTimes} />
                </Link>
              </ListItemText>
            </List>
            <p id='transition-modal-title'
              style={{ fontSize: '20px' }}>
              <b>Upload Picture</b>
            </p>
            <ImageCropper
              onUpoad={handleUpload}
              photo={temp64}
              handleAccountPhoto={handleAccountPhoto}
              classes={classes}
              classes2={classes}
              setPhoto={setTemp64}
              closeUploadModal={closeUploadModal} />
          </div>
        </Fade>
      </Modal>

      <div className={classes.main}>
        {loading
          ? (
            <div className={classes.root}>
              <div className={classes.container}>
                <CircularProgress size={150} />
              </div>
            </div>
          ) : null
        }
        <Grid container xs={12}>
          <Grid item xs={3} className={classes.sideBarContainer}>
            <IconButton edge='end' onClick={() => { }}>
              <Avatar
                className={classes.userImage}
                src={profilePic64 ? `data:image/jpeg;base64,${profilePic64}` : !previousProfilePic ? 'profile_pic-01.png' : previousProfilePic}
                alt='avatar' />
              {isToUpdate && <span
                onClick={openUploadModal}
                className={classes.editIconContainer}>
                <CreateIcon className={classes.editIcon} />
              </span>}
            </IconButton>
          </Grid>
        </Grid>
      </div>
    </React.Fragment>
  )
}
