import React from 'react'
import DialogContent from '@material-ui/core/DialogContent'
import classes from '../scss/TableDisplay.module.scss'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import { getNestedObjects, getNestedArrayOfObjects } from 'Pages/Protected/Partner/Components/Utils/utils'
import { CSVLink } from 'react-csv'
import InfiniteScroll from 'react-infinite-scroll-component'
import TablePreview from './TablePreview'

const PreviewDownload = (props) => {
  const { openPreview, setOpenPreview, headerColumn = [], filename, type = 'HOSPITAL', mongoSelect = {}, filter, infiniteScrollService, downloadFunc, ahmopiType, forContractRenewal } = props
  const [csvDownload, setCsvDownload] = React.useState([])
  const [tablePreviewData, setTablePreviewData] = React.useState([])
  const [hasMore, setHasMore] = React.useState(true)
  const [totalCount, setTotalCounts] = React.useState(null)
  const [{ offset, limit }, setInfiniteScroll] = React.useState({
    offset: 0,
    limit: 15
  })

  React.useEffect(() => {
    csvFormat()
  }, [tablePreviewData])

  React.useEffect(() => {
    if (typeof infiniteScrollService === 'function') {
      infiniteScrollService(setTablePreviewData, tablePreviewData, 0, 15, setTotalCounts, filter, type, ahmopiType)
    }
  }, [])

  const csvFormat = async () => {
    try {
      if (typeof downloadFunc === 'function') {
        const { data: { data } } = await downloadFunc({ select: mongoSelect, filter: JSON.stringify(filter), type, ahmopiType })

        if ((type === 'HOSPITAL' || type === 'CLINIC') && !forContractRenewal && !ahmopiType) {
          const newData = data.map(items => {
            return {
              'Status': getNestedObjects(items, ['status']),
              'Facility Level': getNestedObjects(items, ['classification']),
              'Medical Partner ID': getNestedObjects(items, ['id']),
              'Medical Clinic Name': getNestedObjects(items, ['name']),
              'Unit No./Street': getNestedObjects(items, ['street']),
              'Barangay': getNestedObjects(items, ['barangay']),
              'City/Municipality': getNestedObjects(items, ['city']),
              'Province': getNestedObjects(items, ['district']),
              'Region': getNestedObjects(items, ['region']),
              'Contact Number': getNestedArrayOfObjects(items, ['contacts']).length > 0
                ? getNestedObjects(items, ['contacts']).map(items => getNestedObjects(items, ['cNumber'])).join(' ,')
                : ''
            }
          })
          setCsvDownload(newData)
        } else if (forContractRenewal) {
          const newData = data.map(items => {
            return {
              'Status': getNestedObjects(items, ['status']),
              'Medical Partner Id': getNestedObjects(items, ['id']),
              'Partner Name': getNestedObjects(items, ['name']),
              'Location': getNestedObjects(items, ['full_address']),
              'Date Of Expiration': getNestedObjects(items, ['expiration_date'])
            }
          })
          setCsvDownload(newData)
        } else {
          setCsvDownload(data)
        }
      }
    } catch (error) {
      console.log('error sa download', error)
    }
  }

  const fetch = async () => {
    if (tablePreviewData.length >= totalCount || 0) {
      setHasMore(false)
      return
    }
    setInfiniteScroll({ offset: offset + limit, limit })
    await infiniteScrollService(setTablePreviewData, tablePreviewData, offset + limit, limit, setTotalCounts, filter, type, ahmopiType)
  }

  return (
    <Dialog fullWidth maxWidth={'xl'} open={openPreview} onClose={() => { setOpenPreview(false) }} aria-labelledby='dialogTitle'>
      <DialogContent style={{ minLength: 400 }}>
        <InfiniteScroll
          dataLength={tablePreviewData.length}
          next={fetch}
          hasMore={hasMore}
          loader={tablePreviewData.length <= 7 ? '' : <h4>Loading...</h4>}
          height={550}
          endMessage={
            <p style={{ textAlign: 'center' }}>
              <b>You have seen it all</b>
            </p>
          }
        >
          <TablePreview headers={headerColumn} dataRow={tablePreviewData} />
        </InfiniteScroll>
      </DialogContent>
      <DialogActions className={classes.DialogActions}>
        <Button
          onClick={() => {
            setOpenPreview(false)
          }}
          variant='contained'
          className={classes.Cancel}
        >
          Close
        </Button>
        <CSVLink data={csvDownload} filename={filename} onClick={() => setOpenPreview(false)} style={{ textDecoration: 'none' }}>
          <Button variant='contained' className={classes.Add}>
            Download
          </Button>
        </CSVLink>
      </DialogActions>
    </Dialog>
  )
}

export default PreviewDownload
